  a {
    position: relative;
    text-decoration: none;
  }
  
  a:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 5px;
    bottom: -20px;
    left: 0;
    background-color: #07137C;
    visibility: hidden;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
  }
  
  .black.active a:before{
    visibility: visible;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }
  
  button {
    background-color: lightgray;
    border: 0;
    cursor: pointer;
    height: 25px;
    position: relative;
    width: 50px;
    -webkit-appearance: none;
    -moz-appearance: none;
    border: 0.9px solid #DDDDDD;
box-shadow: 0px 2px 12px rgba(0, 3, 61, 0.0557528);
border-radius: 19px;
margin-left: .7rem;
  }
  
  button:hover { 
    background-color: lightgray; 
  }
  button:focus,
  button:active { 
    outline: none;
   }
  button::-moz-focus-inner { 
    border: 0; 
  }
  
  .pin {
    background-color: white;
   
    border-radius: 50%;
    height: 20px;
    left: 4px;
    position: absolute;
    top: .7px;
    width: 20px;
    transition: left ease .3s;
  }

  
  
  button.on {
    content: "ON";
  }

  button.off{
    content: "OFF";
  }
  
  button.on .pin {
    left: 50%;
  }
  
  @media (min-width:300px) and (max-width: 800px){
    .leftarrowed{
      display: none !important;
